@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Circe";
  font-display: swap;
  src: url("../fonts/Circe-Regular.woff2") format("woff2"), url("../fonts/Circe-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Circe";
  font-display: swap;
  src: url("../fonts/Circe-ExtraBold.woff2") format("woff2"), url("../fonts/Circe-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "CirceRounded";
  font-display: swap;
  src: url("../fonts/CirceRounded-Regular3.woff2") format("woff2"), url("../fonts/CirceRounded-Regular3.woff") format("woff");
}
html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 400;
  font-family: "Circe", "Arial", sans-serif;
  color: #3f3f3f;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 360px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 16px;
  line-height: 21px;
  background-color: #fcf9ed;
}

.body--locked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1560px;
  margin: 0 auto;
  padding: 0 100px;
}
@media (max-width: 1024px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.page-header {
  font-size: 16px;
  line-height: 16px;
  min-width: 360px;
  color: #ffffff;
  margin-bottom: 24px;
  margin-top: 16px;
  padding: 0 100px;
}
@media (max-width: 1024px) {
  .page-header {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .page-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    padding: 0 16px;
  }
}
.page-header--index {
  position: absolute;
  width: 100%;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
  background-color: #336f3f;
}
.page-header--index .page-header__container {
  border-radius: 0;
  max-width: 1560px;
  padding: 12px 100px;
}
@media (max-width: 1024px) {
  .page-header--index .page-header__container {
    padding-right: 45px;
    padding-left: 45px;
  }
}
@media (max-width: 767px) {
  .page-header--index .page-header__container {
    padding: 20px 16px 10px;
  }
}
.page-header--index .page-header__logo {
  display: none;
}
@media (max-width: 850px) {
  .page-header--index .site-nav__item {
    margin-right: 60px;
  }
}
.page-header--index .page-header__nav-toggle {
  display: none;
  margin-left: auto;
  background-image: url("../img/svg/icon-index-burger.svg");
  padding: 18px 14px;
  border-radius: 16px;
  background-color: #fbec51;
}
@media (max-width: 767px) {
  .page-header--index .page-header__nav-toggle {
    display: flex;
  }
}

.page-header--nav-open {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  min-height: 100vh;
  background-color: #4f965d;
  margin: 0;
  padding: 0;
  padding-top: 12px;
  overflow: auto;
}
.page-header--nav-open .page-header__logo-wrapper {
  margin-bottom: 28px;
  padding: 14px 0 14px 24px;
}
.page-header--nav-open .site-nav {
  display: flex;
}
.page-header--nav-open .site-nav__list {
  display: flex;
}
.page-header--nav-open .page-header__nav-toggle {
  background-image: url("../img/svg/icon-cross.svg");
  background-color: transparent;
}
.page-header--nav-open .page-header__container {
  padding: 0 16px;
}
@media (max-width: 767px) {
  .page-header--nav-open .page-header__container {
    background-color: transparent;
  }
}
.page-header--nav-open .page-header__logo {
  display: flex;
}
.page-header--nav-open .contacts {
  display: flex;
}

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1360px;
  padding: 16px 40px;
  align-items: center;
  border-radius: 24px;
  background-color: #336f3f;
}
@media (max-width: 1024px) {
  .page-header__container {
    padding-right: 45px;
    padding-left: 45px;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: auto;
    padding: 14px 16px 14px 24px;
  }
}

.page-header__logo-wrapper {
  display: flex;
}
@media (max-width: 767px) {
  .page-header__logo-wrapper {
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: #336f3f;
    border-radius: 16px;
  }
}

.page-header__logo {
  margin-right: 20px;
}
@media (max-width: 767px) {
  .page-header__logo svg {
    width: 116px;
    height: 36px;
  }
}

@media (max-width: 767px) {
  .site-nav {
    display: none;
    width: 100%;
  }
}

.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .site-nav__list {
    flex-direction: column;
  }
}

.site-nav__item {
  margin-right: 60px;
}
.site-nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 850px) {
  .site-nav__item {
    margin-right: 40px;
  }
}
.site-nav__item a {
  display: flex;
  padding: 12px 0;
  text-decoration: none;
  position: relative;
}
.site-nav__item a:not(:hover) {
  transition: 0.3s;
}
@media (min-width: 1025px) {
  .site-nav__item a:hover {
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    color: #fbec51;
    margin-top: -5px;
  }
  .site-nav__item a:hover::after {
    content: "";
    position: absolute;
    background-image: url("../img/svg/icon-active-menu.svg");
    background-repeat: no-repeat;
    width: 55px;
    height: 8px;
    bottom: 7px;
  }
}
@media (max-width: 767px) {
  .site-nav__item {
    margin-right: 0;
    margin-bottom: 16px;
  }
  .site-nav__item a {
    font-size: 32px;
    line-height: 32px;
  }
}

.page-header__nav-toggle {
  background-image: url("../img/svg/icon-white-burger.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 48px;
  height: 48px;
  border: 0;
  outline: 0;
  background-color: transparent;
  display: none;
}
@media (max-width: 767px) {
  .page-header__nav-toggle {
    display: flex;
  }
}

.intro {
  display: flex;
  flex-direction: column;
  background-color: #336f3f;
  padding: 90px 0 40px;
  min-height: 100vh;
}
@media (max-width: 767px) {
  .intro {
    padding-top: 78px;
  }
}

.intro__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.intro__wrapper {
  display: flex;
  margin-bottom: 24px;
  position: relative;
}
.intro__wrapper::after {
  content: "";
  position: absolute;
  background-image: url("../img/index/company-products.png");
  background-repeat: no-repeat;
  width: 495px;
  height: 269px;
  bottom: -50px;
  left: 0;
}
@media (max-width: 1500px) {
  .intro__wrapper::after {
    width: 330px;
    height: 269px;
    bottom: -50px;
    background-size: contain;
  }
}
@media (max-width: 1024px) {
  .intro__wrapper::after {
    display: none;
  }
}
@media (max-width: 1024px) {
  .intro__wrapper {
    flex-direction: column;
  }
}

.intro__logo {
  display: flex;
  width: 31.9%;
  margin-right: 3.67%;
}
@media (max-width: 1024px) {
  .intro__logo {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .intro__logo svg {
    height: auto;
  }
}
@media (max-width: 767px) {
  .intro__logo {
    width: 100%;
    margin-right: 0;
    margin-bottom: 33px;
  }
  .intro__logo svg {
    width: 244px;
    height: 76px;
  }
}

.intro__info {
  background-color: #4f965d;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  width: 64.4%;
  position: relative;
}
.intro__info::before {
  content: "";
  position: absolute;
  background-image: url("../img/index/natural-composition-desktop.png");
  background-repeat: no-repeat;
  width: 195px;
  height: 65px;
  top: -13px;
  right: 68px;
}
@media (max-width: 767px) {
  .intro__info::before {
    background-image: url("../img/index/natural-composition-mobile.png");
    background-size: contain;
    width: 160px;
    height: 56px;
    top: -29px;
    right: 46px;
  }
}
.intro__info::after {
  content: "";
  position: absolute;
  background-image: url("../img/index/cat-index-desktop.png");
  background-repeat: no-repeat;
  width: 192px;
  height: 192px;
  bottom: -108px;
  right: 43px;
}
@media (max-width: 1500px) {
  .intro__info::after {
    width: 150px;
    height: 150px;
    bottom: -95px;
    right: 18px;
    background-size: contain;
  }
}
@media (max-width: 767px) {
  .intro__info::after {
    width: 88px;
    height: 88px;
    bottom: -53px;
  }
}
.intro__info h2 {
  font-size: 22px;
  line-height: 26px;
  color: #ffffff;
  margin: 0;
  font-weight: 400;
  padding: 40px 0 0 40px;
  margin-bottom: 36px;
}
@media (max-width: 767px) {
  .intro__info h2 {
    font-size: 18px;
    line-height: 22px;
    padding: 40px 33px 0 20px;
    margin-bottom: 24px;
  }
}
.intro__info p {
  font-size: 68px;
  line-height: 61px;
  font-family: "CirceRounded", "Arial", sans-serif;
  color: #fbfbfb;
  margin: 0;
  padding: 0 40px 40px;
}
@media (max-width: 1500px) {
  .intro__info p {
    font-size: 58px;
    line-height: 51px;
  }
}
@media (max-width: 767px) {
  .intro__info p {
    font-size: 42px;
    line-height: 38px;
    padding: 0 20px 40px 20px;
  }
}
@media (max-width: 1024px) {
  .intro__info {
    width: 100%;
  }
}

.intro__image {
  flex-grow: 1;
  display: block;
  object-fit: cover;
  border-radius: 40px;
}

.advantages {
  background-color: #f8f0d4;
  padding-top: 130px;
  padding-bottom: 130px;
}
@media (max-width: 1024px) {
  .advantages {
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.advantages__description {
  padding-bottom: 148px;
  background-image: url("../img/description-bg.png");
  background-repeat: no-repeat;
  background-position: left 130px;
}
@media (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .advantages__description {
    background-image: url("../img/description-bg@2x.png");
    background-size: 1190px auto;
  }
}
@media (max-width: 1024px) {
  .advantages__description {
    background-image: none;
  }
}
@media (max-width: 767px) {
  .advantages__description {
    padding-bottom: 90px;
  }
}
.advantages__description h2 {
  font-size: 60px;
  line-height: 60px;
  font-family: "CirceRounded", "Arial", sans-serif;
