}
@media (max-width: 767px) {
  .advantages__description h2 {
    font-size: 38px;
    line-height: 38px;
    margin-bottom: 90px;
  }
}

.advantages__slider {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
}
@media (max-width: 1024px) {
  .advantages__slider {
    flex-wrap: nowrap;
    justify-content: flex-start;
    width: 100%;
  }
}

.advantages__slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  width: 30.44%;
