  margin: 0;
  text-align: center;
  padding: 76px 24px 30px;
  z-index: 1;
}
@media (max-width: 767px) {
  .advantages__slide p {
    font-size: 18px;
    line-height: 23px;
    padding-bottom: 40px;
  }
}
.advantages__slide::after {
  content: "";
  position: absolute;
  transition: 0.3s;
  width: 520px;
  height: 320px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}
@media (max-width: 1455px) {
  .advantages__slide::after {
    width: 490px;
  }
}
@media (max-width: 1290px) {
  .advantages__slide::after {
    width: 400px;
  }
}
@media (max-width: 1024px) {
  .advantages__slide::after {
    display: none;
  }
}
@media (max-width: 767px) {
  .advantages__slide::after {
    width: 348px;
    height: 268px;
    display: flex;
  }
}
@media (min-width: 1025px) {
  .advantages__slide:hover::after {
    outline: none;
    transition: 0.3s;
    opacity: 1;
  }
}
@media (min-width: 1025px) {
  .advantages__slide--standart:hover::after {
    background-image: url("../img/index/advantage-standart-hover.png");
  }
}
@media (min-width: 1025px) {
  .advantages__slide--hypoallergic:hover::after {
    background-image: url("../img/index/advantage-hypoallergic-hover.png");
  }
}
