.modal {
  animation-name: smooth;
  animation-duration: 0.3s;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 20;
  display: flex;
  justify-content: center;
  width: 100vw;
  min-width: 360px;
  padding: 220px 100px;
  background-color: rgba(0, 0, 0, 0.7);
  overflow-x: hidden;
  overflow-y: auto;
}
.modal.modal--hidden {
  display: none;
}

.modal__close {
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  position: absolute;
  right: 0;
  top: -56px;
  width: 48px;
  height: 48px;
}
.modal__close:not(:hover) {
  transition: 0.3s;
}
.modal__close:hover {
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  opacity: 0.5;
}

@keyframes smooth {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
